export default {
	data() {
		return {
			head: "",
			nick: "",
			sno: "",
			realName: "",
			gender: "",
			phone: ""
		}
	},
	methods: {
		tap: function() {
			wx.navigateBack({})
		},
		requestData() {
			this.$api.getUserInfo().then(res => {
				this.head = res.data.headPortrait;
				this.nick = res.data.nickName;
				this.sno = res.data.sno;
				this.realName = res.data.realName;
				if (res.data.sex == 1) {
					this.gender = "男"
				} else if (res.data.sex == 2) {
					this.gender = "女"
				} else {
					this.gender = "未知"
				}
				this.phone = res.data.phone;
			})
		}
	},
	created() {
		this.requestData()
	}
}
